@import url('https://api.fontshare.com/v2/css?f[]=chillax@500&display=swap');

.App {
  text-align: center;
}

.App-logo {
  height: 12vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 15vh;
  display: flex;
  // flex-direction: column;
  align-items: center;
  justify-content: center;
  // font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.components {
  // width: 100%;
  // margin-left: auto;
  // margin-right: auto;
  // margin-top: 50px;
  display: flex;
  flex-direction: column;
  background-color: #282c34;
  // border-radius: 3px;
  // box-shadow: 5px 10px 20px rgba(0, 0, 0, 0.4);
  position: relative;
  opacity: 0.9s;

  @media (min-width: 600px) {
    width: 85%;
    height: 500px;
    overflow-y: scroll;
    margin-left: auto;
    margin-right: auto;
    margin-top: 50px;
    display: flex;
    flex-direction: row;
    background-color: #282c34;
    border-radius: 3px;
    box-shadow: 5px 10px 20px rgba(0, 0, 0, 0.4);
    position: relative;
    opacity: 0.9s;
  }
}

h1 {
  color: #282c34;
}

h2 {
  font-family: 'Chillax', sans-serif;
}

.vert-line {
  @media (min-width: 600px) {
    min-height: 1000px;
    border-left: 6px solid #61dbfb;
  }
}
