@import url('https://api.fontshare.com/v2/css?f[]=chillax@500&display=swap');

.form-input {
  margin: 5px;
  width: 100%;

  @media (min-width: 600px) {
    margin: 5px;
    width: 300px;
  }

  input {
    width: 300px;
    padding: 4px;
    font-size: 1.5rem;
    font-size: 1.5rem;
    font-weight: 600;
    border: 2px solid #61dbfb;
    border-radius: 3px;
    font-family: 'Chillax', sans-serif;

    @media (min-width: 600px) {
      width: 220px;
      padding: 4px;
      // text-align: center;
      font-size: 1.5rem;
      font-weight: 600;
      border: 2px solid #61dbfb;
      border-radius: 3px;
      font-family: 'Chillax', sans-serif;
      // display: block;
    }
  }

  input:focus {
    outline: none;
  }

  .btn {
    width: 50px;
    height: 40px;
    padding: 9px 5px;
    margin-top: 5px;
    margin-left: 5px;
    color: #282c34;
    font-size: 1.2rem;
    font-weight: 600;
    background-color: #61dbfb;
    border: 2px solid #61dbfb;
    border-radius: 3px;
    cursor: pointer;
  }

  .btn:hover {
    opacity: 0.8;
  }
}
